/* 
our main class that bootstraps everything =]
*/
export default class Sketch {
  constructor(options) {
    /* 
    below are a couple of varaibles that are gloabal across this class, we use these variables to
    track some things and adding things to settings etc. ^-^
    */
    this.container = options.dom;

    this.debug = true;

    this.debugLog = new DebugLog(this);

    this.sizes = {
      width: window.innerWidth,
      height: window.innerHeight,
    };

    this.showSettings = true;

    this.debugObject = {
      rendererParams: {},
      controllerParams: {},
      charachterParams: {
        velocityDampeningAmount: new Vector2(10, 10),
        jumpAmount: 350,
        gravity: 9.8,
        mass: 100,
        moveAmount: new Vector2(2000, 2000),
        eyeLevel: 40,
      },
    };

    this.rotation = 0;

    this.mouse = {
      x: 0,
      y: 0,
      prevX: 0,
      prevY: 0,
      vX: 0,
      vY: 0,
    };

    /* 
    an array that holds objects that are used to do our raycasting for jumping :)

    Note : please try to look for object.push(...) inside init.js, those objects are the
           ones we want to track for our raycasting and jumping :}
    */
    this.objects = [];

    /* 
    setting up the HTML side of things and setting up the global variable bridge
    between the HTML and threejs systems :)

    please read comments and explanations in the order of initialization and calls for a clear
    picture
    */
    this.G = new GlobalVarsInit();
    new HTMLInitializer(this.G);
    new GlobalLoaderManager(this);

    this.videoManager = new VideoManager(this);

    /* 
    setting up the threejs part of things :/
    */
    this.setup();
  }

  async setup() {
    /* 
    please check out the respective sources for explanations of what this modules do, 
    we basically instantiate a new object i.e call whatever code is setup in the constructor
    of these classes :)

    please read in the order they are initialized to get a clearer picture, 

    */

    this.globalSettings = {
      renderCannonDebug: false,
    };

    new Settings(this);

    this.physicsManager = new PhysicsManager(this);

    new BasicScene(this);

    this.cannonDebugger = new CannonDebugger(this.scene, this.world);

    // new InitShowRoom(this);

    // this.debugLog.logInfo(this.controls);

    this.movePlayer();
    this.render();
  }

  moveCamera(delta) {
    // var delta = clock.getDelta();
    let sensitivity = 150;
    // let rotateAngle = Math.PI / 2 * delta * sensitivity;
    /*  if (pressed['A'] || pressed['ARROWLEFT']) {
        rotation -= rotateAngle;
    }
    if (pressed['D'] || pressed['ARROWRIGHT']) {
        rotation += rotateAngle;
    } */
    // let rotZ = Math.cos(this.rotation);
    // let rotX = Math.sin(this.rotation);
    let distance = 20;
    this.camera.position.x = this.player.position.x;
    this.camera.position.y = this.player.position.y + 1.25;
    this.camera.position.z = this.player.position.z + 3;
    this.camera.lookAt(this.player.position);
  }

  movePlayer() {
    let lerpVal = 0.75;
    window.addEventListener("keypress", (e) => {
      switch (e.code) {
        case "KeyA":
          gsap.to(this.player.position, { x: -2, duration: 0.5 });
          // this.player.position.lerp(new Vector3(-8, 2.5, 0), lerpVal);
          break;

        case "KeyD":
          gsap.to(this.player.position, { x: 2, duration: 0.5 });
          break;

        case "KeyS":
          gsap.to(this.player.position, { x: 0, duration: 0.5 });
          break;

        default:
          break;
      }
    });
  }

  jump() {
    let finalPosition = 2;
    let time = {
      start: null,
      total: 250,
    };

    let obj = document.querySelector(".foo");
    const moveup = (now) => {
      if (!time.start) time.start = now;
      time.elapsed = now - time.start;
      let progress = time.elapsed / time.total;
      let position = progress * finalPosition;
      this.player.position.y = position;
      if (progress < 1) {
        requestAnimationFrame(moveup);
      } else {
        time.start = 0;
        requestAnimationFrame(movedown);
      }
    };

    const movedown = (now) => {
      if (!time.start) time.start = now;
      time.elapsed = now - time.start;
      let progress = time.elapsed / time.total;
      let position = finalPosition * (1 - progress);
      this.player.position.y = position;
      if (progress < 1) {
        requestAnimationFrame(movedown);
      } else {
        time.start = 0;
      }
    };

    function jump() {
      requestAnimationFrame(moveup);
    }

    document.addEventListener(
      "keypress",
      (e) => {
        if (e.code === "Space") jump();
      },
      false
    );
  }

  /* 
  this is the same old render function, it basically do updates of things that need to be
  updated in a frame by frame basis
  */
  render() {
    // return;
    this.time = this.clock.getDelta();
    this.stat.update();

    this.jump();

    // if (this.controls.enabled) {
    //   this.world.step(1 / 60, this.time);

    //   for (let i = 0; i < this.balls.length; i++) {
    //     this.ballMeshes[i].position.copy(this.balls[i].position);
    //     this.ballMeshes[i].quaternion.copy(this.balls[i].quaternion);
    //   }
    //   if (this.globalSettings.renderCannonDebug) {
    //     this.cannonDebugger.update();
    //   }
    // }

    this.renderer.render(this.scene, this.camera);

    this.controls.update();
    if (this.mixer) this.mixer.update(this.time);

    // this.camera.position.z
    this.player.position.z -= 0.125;

    this.moveCamera(this.time);

    // this.camera.position.copy(this.player.position);

    window.requestAnimationFrame(this.render.bind(this));

    this.stat.end();
  }
}

