/* 
just a normal import from threejs, please note that we only imported the modules
we need to have an optimized build later
*/
import {
  CylinderGeometry,
  MeshPhongMaterial,
  Mesh,
  PCFSoftShadowMap,
  Scene,
  Fog,
  Color,
  Clock,
  PerspectiveCamera,
  AnimationAction,
  AnimationClip,
  AnimationMixer,
  AmbientLight,
  WebGLRenderer,
  WebGLRenderTarget,
  DirectionalLight,
  sRGBEncoding,
  PlaneBufferGeometry,
  BoxBufferGeometry,
  MeshBasicMaterial,
  FogExp2,
  Group,
} from "three";

import Stats from "stats-js";

/* 
CharacterController is a module that handles our pointer lock control and the jumping + 
raycasting system of our navigation and movement
*/
import CharacterController from "./charachterController";
import AudioManager from "./audio_management/audio";

import GLTFmodelLoader from "./loaders/GLTFmodelLoader";

export default class BasicScene {
  constructor(context) {
    /* 
    you might noticed and wondered that we passed a 'context' argument for the constructor
    of the current class, and when we initialized the BasicScene class we passed 'this' as
    an argument like :
    => new BasicScene(this);

    think of the 'context' as the main thread of the threejs experiance, or the context is 
    the main class that orchestrate the whole show, and the smaller modules like this one 
    needs to get the context of that main class so that they can latch on and inject their
    own little logic into the main logic of the main logic.(please remind me to dive a bit
      deeper on this on our next meeting, this is an exteremely important concept)
    */
    this.context = context;

    this.settings = {};

    this.gltfModelLoader = new GLTFmodelLoader(this.context);

    /* 
    the order and the purpose of the following functions are pretty straight forward, please
    remind me to clarify on anything that is not clear
    */

    this.setupRenderer();

    this.setupClock();

    this.setupStats();

    this.createScene();

    this.resize();

    this.setupResize();

    this.mouseEvents();

    this.addClassSettings();
  }

  async sleep(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  async createScene() {
    this.context.scene = new Scene();

    const near = 0.1;
    const far = 100;
    const color = "#ccc";
    // this.context.scene.fog = new Fog(color, near, far);
    this.context.scene.fog = new FogExp2(color, 0.025);
    this.context.scene.background = new Color("#bbb");

    this.setupCameras();

    this.setupLights();

    this.setupAudioManager();

    /* 
    this bit below just initializes our charachter/camera controller 
    please go to the module for more info

    it basically handles WASD/ARROW/SPACE buttons and the psudo-physics we have now
    
    */

    const ambientLight = new AmbientLight(0x222222);
    this.context.scene.add(ambientLight);

    this.context.playerController = new CharacterController(this.context);

    // await this.loadLanscapes();

    this.addPlayerMesh();

    this.runLandscapeLoop();
  }

  setupRenderer() {
    this.context.renderer = new WebGLRenderer({ antialias: true });
    this.context.renderer.setSize(
      this.context.sizes.width,
      this.context.sizes.height
    );
    this.context.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
    this.context.renderer.setClearColor("#262837", 1);
    this.context.renderer.sRGBEncoding = true;
    this.context.renderer.shadowMap.type = PCFSoftShadowMap;
    this.context.renderer.physicallyCorrectLights = true;
    this.context.renderer.autoClear = false;
    this.context.renderer.antialias = false;
    this.context.renderer.physicallyCorrectLights = true;
    this.context.renderer.textureEncoding = sRGBEncoding;
    this.context.renderer.outputEncoding = sRGBEncoding;
    this.context.container.appendChild(this.context.renderer.domElement);

    this.context.envFBO = new WebGLRenderTarget(
      this.context.sizes.width,
      this.context.sizes.height
    );

    this.context.backfaceFBO = new WebGLRenderTarget(
      this.context.sizes.width,
      this.context.sizes.height
    );
  }

  setupClock() {
    this.context.clock = new Clock();
    this.context.time = 0;
  }

  setupStats() {
    this.context.stat = new Stats();
    this.context.stat.showPanel(0);
    document.body.appendChild(this.context.stat.dom);
  }

  async setupCameras() {
    this.context.camera = new PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      10000
    );

    this.context.camera.position.set(0, 10, 30);
    this.context.camera.lookAt(0, 3, 5);
    this.context.scene.add(this.context.camera);
  }

  async setupLights() {
    this.context.ambLight = new AmbientLight("#fff", 0.5);
    this.context.scene.add(this.context.ambLight);

    this.context.moonLight = new DirectionalLight("#fff", 2.5);
    this.context.moonLight.position.set(0, 4, 0);

    this.context.scene.add(this.context.moonLight);
  }

  setupAudioManager() {
    /* 
    todo : add more initialization logic here later
    */
    this.context.audioManager = new AudioManager(this.context);
  }

  mouseEvents() {
    window.addEventListener("mousemove", (e) => {
      this.context.mouse.x = e.clientX / this.context.sizes.width;
      this.context.mouse.y = e.clientY / this.context.sizes.height;

      this.context.mouse.vX = this.context.mouse.x - this.context.mouse.prevX;
      this.context.mouse.vY = this.context.mouse.y - this.context.mouse.prevY;

      this.context.mouse.prevX = this.context.mouse.x;
      this.context.mouse.prevY = this.context.mouse.y;
    });
  }
  setupResize() {
    window.addEventListener("resize", this.resize.bind(this));
  }

  resize() {
    this.context.sizes.width = window.innerWidth;
    this.context.sizes.height = window.innerHeight;
    this.context.renderer.setSize(
      this.context.sizes.width,
      this.context.sizes.height
    );
    this.context.camera.aspect =
      this.context.sizes.width / this.context.sizes.height;

    // image cover please uncomment this when using ortho camera
    /* this.imageAspect = 1;
    let a1;
    let a2;
    if (this.sizes.height / this.sizes.width > this.imageAspect) {
      a1 = (this.sizes.width / this.sizes.height) * this.imageAspect;
      a2 = 1;
    } else {
      a1 = 1;
      a2 = this.sizes.height / this.sizes.width / this.imageAspect;
    }

    this.material.uniforms.resolution.value.x = this.sizes.width;
    this.material.uniforms.resolution.value.y = this.sizes.height;
    this.material.uniforms.resolution.value.z = a1;
    this.material.uniforms.resolution.value.w = a2; */

    this.context.camera.updateProjectionMatrix();
  }

  async loadLanscapeType1() {
    //load the first variation of the landscapes here
    let { model } = await this.gltfModelLoader.loadModel(
      "/lanscapes/landscape_1.glb"
    );

    return model;
  }

  async loadLanscapeType2() {
    //load the second variation of the landscapes here
    let { model } = await this.gltfModelLoader.loadModel(
      "/lanscapes/landscape_2.glb"
    );

    return model;
  }

  loadLanscapeType3() {
    //load the third variation of the landscapes here
  }

  async loadPlayerModel() {
    let { model, animations } = await this.gltfModelLoader.loadModel(
      "/player/Swat.gltf"
    );

    return {
      model,
      animations,
    };
  }

  async loadLanscapes() {}

  async addPlayerMesh() {
    // load the player charachter and play runnung animations here
    // let playerCubeGeo = new BoxBufferGeometry(0.125, 0.5, 0.125);
    // let playerCubeMat = new MeshBasicMaterial({ color: "green" });
    // this.context.player = new Mesh(playerCubeGeo, playerCubeMat);
    this.context.player = new Group();
    this.context.playerModelAnim = await this.loadPlayerModel();
    console.log(this.context.playerModelAnim);
    this.context.playerMesh = this.context.playerModelAnim.model;

    this.context.mixer = new AnimationMixer(this.context.playerMesh);
    this.context.clips = this.context.playerModelAnim.animations;

    console.log(this.context.mixer);

    this.context.player.add(this.context.playerMesh);

    this.context.player.rotation.set(0, Math.PI, 0);
    this.context.player.scale.setScalar(0.5);
    console.log(this.context.player);

    this.clip = AnimationClip.findByName(this.context.clips, "run");
    console.log(this.clip);
    const action = this.context.mixer.clipAction(this.context.clips[16]);
    action.play();

    // this.context.player.position.y = 2.5;
    this.context.scene.add(this.context.player);
  }

  randomIntFromInterval(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min);
  }

  async runLandscapeLoop() {
    this.landscapes = [];

    this.landspace1 = await this.loadLanscapeType1();
    this.landscapes.push(this.landspace1);
    this.landspace2 = await this.loadLanscapeType2();
    this.landscapes.push(this.landspace2);

    let z = 0;

    console.log(this.landscapes);

    let number = 0;
    let meshToAdd;
    while (true) {
      if (this.randomIntFromInterval(0, 1)) {
        meshToAdd = this.landscapes[1].children[0].clone();
      } else {
        meshToAdd = this.landscapes[0].children[0].clone();
      }
      // let meshToAdd = await this.loadLanscapeType1();
      meshToAdd.position.z = z;

      this.context.scene.add(meshToAdd);

      z -= 36;
      // number += 1;
      await this.sleep(2 * 1000);
    }
  }

  addClassSettings() {
    let todo = { todo: "WILL ADD LATER" };
    let localSettings = this.context.gui.addFolder("SCENE SETTINGS");
    localSettings.add(todo, "todo").name("");
  }
}
